.popup__input {
  box-sizing: border-box;
  width: 358px;
  border: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding: 0 0 8px 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
}

@media screen and (max-width: 720px) {
  .popup__input {
    width: 238px;
  }
}
