.header {
  max-width: 880px;
  margin: 45px auto 0 auto;
  padding: 0;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

@media screen and (max-width: 917px) {
  .header {
    margin: 28px auto 0 auto;
  }
}
